import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const TotalPackageAmount = React.memo(props => {
  const { handleChange, totalPackageAmount } = props;
  return (
    <TextValidator
      required
      name="totalPackageAmount"
      id="totalPackageAmount"
      onChange={handleChange}
      value={totalPackageAmount}
      label="Total Package Amount"
      helperText=""
      placeholder="00000000"
      fullWidth
      validators={[
        'required',
        'minNumber:0',
        'maxNumber:99999999',
        'matchRegexp:^[0-9]*$',
      ]}
      errorMessages={[
        '',
        ' 0',
        ' 8 ',
        '',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default TotalPackageAmount;
