import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyBranchField = React.memo(props => {
  const { handleChange, companyBranch } = props;
  return (
    <TextValidator
      name="companyBranch"
      id="companyBranch"
      required
      type="number"
      onChange={handleChange}
      value={companyBranch}
      label=""
      placeholder="000000"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[0-9]*$',
        'maxStringLength:6',
      ]}
      errorMessages={[
        '',
        ' 0-9',
        ' 6 ',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyBranchField;
