import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const ManagerName = React.memo(props => {
  const { handleChange, managerName } = props;
  return (
    <TextValidator
      name="managerName"
      id="managerName"
      required
      onChange={handleChange}
      value={managerName}
      label="Manager Name"
      helperText=" |  Broker"
      fullWidth
      validators={['required', 'maxStringLength:35']}
      errorMessages={['', ' 35 ']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default ManagerName;
