import React from 'react';
import { SelectValidator } from 'react-material-ui-form-validator';
import { MenuItem } from '@material-ui/core';

const documentTypes = [
  {
    value: 0,
    label: '0  ',
  },
  {
    value: 3,
    label: '3  ',
  },
  {
    value: 5,
    label: '5 - ',
  },
  {
    value: 'X',
    label: 'X   (Express)',
  },
];

const DocumentTypeField = React.memo(props => {
  const { handleChange, documentType } = props;
  return (
    <SelectValidator
      id="documentType"
      required
      name="documentType"
      onChange={handleChange}
      value={documentType}
      select
      label=""
      fullWidth
      validators={['required']}
      errorMessages={['']}
      variant="outlined"
      margin="dense"
    >
      {documentTypes.map(option => (
        <MenuItem key={option.value} value={option.value}>
          {option.label}
        </MenuItem>
      ))}
    </SelectValidator>
  );
});

export default DocumentTypeField;
