import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CustomsClearanceName = React.memo(props => {
  const { handleChange, customsClearanceName } = props;
  return (
    <TextValidator
      name="customsClearanceName"
      id="customsClearanceName"
      required
      onChange={handleChange}
      value={customsClearanceName}
      label="Customs Clearance Name"
      helperText=""
      fullWidth
      validators={['required', 'maxStringLength:35']}
      errorMessages={['', ' 35 ']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CustomsClearanceName;
