import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const TaxCardBankBranchCode = React.memo(props => {
  const { handleChange, taxCardBankBranchCode } = props;
  return (
    <TextValidator
      required
      name="taxCardBankBranchCode"
      id="taxCardBankBranchCode"
      onChange={handleChange}
      value={taxCardBankBranchCode}
      label="Tax Card Bank Branch Code"
      helperText=""
      placeholder="000000"
      fullWidth
      validators={['required', 'maxStringLength: 6']}
      errorMessages={['', ' 6 ']}
      margin="dense"
      variant="outlined"
    />
  );
});

export default TaxCardBankBranchCode;
