import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const TotalDepositAmount = React.memo(props => {
  const { handleChange, totalDepositAmount } = props;
  return (
    <TextValidator
      required
      name="totalDepositAmount"
      id="totalDepositAmount"
      onChange={handleChange}
      value={totalDepositAmount}
      label="Total Deposit Amount"
      helperText=""
      placeholder="0000.00"
      fullWidth
      validators={['required', 'isFloat', 'isCorrectPaymentAmount']}
      errorMessages={[
        '',
        '',
        ' 16   2 ',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default TotalDepositAmount;
