import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const BrokerBranch = React.memo(props => {
  const { handleChange, brokerBranch } = props;
  return (
    <TextValidator
      name="brokerBranch"
      id="brokerBranch"
      onChange={handleChange}
      value={brokerBranch}
      label="Broker Branch"
      helperText=" Broker |  0"
      fullWidth
      validators={['matchRegexp:^[0-9]*$', 'maxStringLength:6']}
      errorMessages={[' 0-9', ' 6 ']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default BrokerBranch;
