import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyNameFieldEN = React.memo(props => {
  const { handleChange, companyNameEN } = props;
  return (
    <TextValidator
      name="companyNameEN"
      id="companyNameEN"
      required
      onChange={handleChange}
      value={companyNameEN}
      label=""
      placeholder="Heavy Stone Express Co., Ltd."
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[^]*$',
        'maxStringLength:70',
      ]}
      errorMessages={[
        '',
        '',
        ' 70 ',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyNameFieldEN;
