import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyNameFieldTH = React.memo(props => {
  const { handleChange, companyNameTH } = props;
  return (
    <TextValidator
      name="companyNameTH"
      id="companyNameTH"
      required
      onChange={handleChange}
      value={companyNameTH}
      label=""
      placeholder="  "
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[^A-Za-z]*$',
        'maxStringLength:120',
      ]}
      errorMessages={[
        '',
        '',
        ' 120 ',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyNameFieldTH;
