import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const BankCode = React.memo(props => {
  const { handleChange, bankCode } = props;
  return (
    <TextValidator
      required
      name="bankCode"
      id="bankCode"
      onChange={handleChange}
      value={bankCode}
      label="Bank Code"
      helperText=""
      placeholder="000"
      fullWidth
      validators={['required', 'isNumber', 'maxStringLength: 3']}
      errorMessages={[
        '',
        '',
        ' 3 ',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default BankCode;
